.btn {
  -webkit-appearance: none;
  background-color: $color--global-light;
  border: 0;
  color: #fff;
  cursor: pointer;
  display: inline-block;
  font-size: pt(14);
  outline: 0;
  padding: 1.8rem 3rem;
  white-space: nowrap;
  @include border-radius(3px);
  &:focus {
    background: lighten($color--global-light, 5%);
  }
  &:hover {
    background: lighten($color--global-light, 5%);
  }
  &:active,
  &.active {
    background: darken($color--global-light, 5%);
    border-color: darken($color--global-light, 10%);
  }
}

label.btn {
  pointer-events: none;
}


.btn-social {
  background-color: $color--global;
  height: 48px;
  font-size: 3rem;
  padding: 0;
  text-align: center;
  width: 49px;
  @include border-radius(6px);
}
